body { margin: 0px; }
.synapse-app {
    background-color: #1F1F1F;
    height: 100vh;
    display: grid;
    grid-template-rows: 70px auto 70px;
}
.synapse-header {
    background-color: #282828;
    border-bottom: #181818 solid 2px;
    color: white;
    font-family: 'Roboto';
    font-size: 20px;
    display: grid;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    grid-template-columns: 1fr 3fr;
    -webkit-column-gap: 10px;
       -moz-column-gap: 10px;
            column-gap: 10px;
}
.synapse-button {
  height: 100%;
  padding: 10px;
  background-color: #282828;
  color: white;
  text-transform: uppercase;
  border: none;
  outline: none;
  font-size: 15px;
  font-family: 'Roboto';
  cursor: pointer;
}
.synapse-button:hover {
  /* color: red; */
  background-color: red;
}
.synapse-button-active {
  background-color: red;
}
.synapse-menu {
  width: 100%; /* min-content */
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.synapse-menu:nth-child(even) {
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;

}
.synapse-content {
    background-color: #1F1F1F;
    overflow-y: scroll;
}
/* width */
::-webkit-scrollbar {
    width: 10px;
  }
/* Track */
::-webkit-scrollbar-track {
    background: #444444;
  }
/* Handle */
::-webkit-scrollbar-thumb {
    background: #1b1b1b;
  }
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #181818;
  }
.synapse-footer {
  background-color: #282828;
  border-top: #181818 solid 2px;
  color: white;
  font-family: 'Roboto';
  font-size: 20px;
}
.synapse-row {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  gap: 5px;
  padding: 5px;
  grid-auto-rows: 1fr;
  background-color: #282828;
  margin: 10px;
}
.synapse-column {
  background-color: #1d1d1d;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: white;
  font-family: 'Roboto';
  padding: 10px;
  font-size: 16px;
}
.synapse-logo {
  font-size: 30px;
  margin-left: 10px;
  white-space: nowrap;
}
@media only screen and (max-width: 800px) {
  .synapse-row {
    grid-template-columns: 1fr;
  }
}